<template>
  <div v-if="model" class="page-hero">
    <!-- 顶部bar -->
    <div class="topbar bg-black py-2 px-3 d-flex text-white ai-center">
      <img src="../assets/images/logo.png" height="30" />
      <div class="d-flex px-2 flex-1">
        <div class="text-white">王者荣耀</div>
        <div class="text-white ml-3">攻略站</div>
      </div>
      <router-link class="fs-xs" to="/" tag="div">更多英雄&emsp;&emsp;&gt;</router-link>
    </div>
    <!-- end 顶部bar -->
    <!-- 英雄基本介绍图 -->
    <div class="top" :style="{'background-image': `url(${model.banner})`}">
      <div class="info text-white p-3 h-100 d-flex flex-colum jc-end">
        <div class="fs-sm">{{model.title}}</div>
        <h2 class="my-2 fs-sm">{{model.name}}</h2>
        <div class="fs-sm">{{model.category.map(v => v.name).join('/')}}</div>
        <div class="d-flex jc-between pt-2">
          <div class="scores d-flex ai-center" v-if="model.scores">
            <span>难度</span>
            <span class="badge bg-primary">{{model.scores.difficult}}</span>
            <span>技能</span>
            <span class="badge bg-blue-1">{{model.scores.skills}}</span>
            <span>攻击</span>
            <span class="badge bg-danger">{{model.scores.attack}}</span>
            <span>生存</span>
            <span class="badge bg-dark">{{model.scores.survive}}</span>
          </div>
          <router-link to="/" tag="span" class="text-grey fs-sm">皮肤：2 &gt;</router-link>
        </div>
      </div>
    </div>
    <!-- end 英雄基本介绍图 -->
    <!-- 详细内容 -->
    <div>
      <div class="bg-white px-3">
        <div class="nav d-flex pt-3 pb-2 jc-around border-bottom">
          <div class="nav-item active">
            <div class="nav-link">英雄初识</div>
          </div>
          <div class="nav-item">
            <div class="nav-link">进阶攻略</div>
          </div>
        </div>
      </div>
      <swiper>
        <swiper-slide>
          <!-- 英雄初识 -->
          <div>
            <!-- 技能列表 -->
            <div class="p-3 bg-white border-bottom">
              <div class="d-flex">
                <router-link tag="button" to="/" class="btn btn-lg flex-1">
                  <i class="iconfont icon-menu"></i>
                  英雄介绍视频
                </router-link>
                <router-link tag="button" to="/" class="btn btn-lg flex-1 ml-2">
                  <i class="iconfont icon-menu"></i>
                  一图识英雄
                </router-link>
              </div>
              <div class="skills bg-white mt-4">
                <div class="d-flex jc-around">
                  <img
                    class="icon"
                    @click="currentSkillIndex = i"
                    :class="{active: currentSkillIndex === i}"
                    :src="item.icon"
                    v-for="(item, i) in model.skills"
                    :key="item.name"
                  />
                </div>
                <div v-if="currentSkill">
                  <div class="d-flex pt-4 pb-3">
                    <h3 class="m-0">{{currentSkill.name}}</h3>
                    <span
                      class="text-grey-1 ml-4"
                    >（冷却值：{{currentSkill.delay}}消耗：{{currentSkill.cost}}）</span>
                  </div>
                  <p class="text-dark-2">{{currentSkill.description}}</p>
                  <div class="border-bottom"></div>
                  <p class="text-grey-1">小提示：{{currentSkill.tips}}</p>
                </div>
              </div>
            </div>
            <!-- end 技能列表 -->
            <m-card plain icon="menu" title="出装推荐" class="hero-items">
              <div class="fs-xl">顺风出装</div>
              <div class="d-flex jc-around text-center mt-3">
                <div v-for="item in model.items1" :key="item.name">
                  <img :src="item.icon" class="icon">
                  <div class="fs-xs">{{item.name}}</div>
                </div>
              </div>
              <div class="border-bottom mt-3"></div>
              <div class="fs-xl mt-3">逆风出装</div>
              <div class="d-flex jc-around text-center mt-3">
                <div v-for="item in model.items2" :key="item.name">
                  <img :src="item.icon" class="icon">
                  <div class="fs-xs">{{item.name}}</div>
                </div>
              </div>
            </m-card>
            <m-card plain icon="menu" title="使用技巧">
              <p class="m-0">{{model.usageTips}}</p>
            </m-card>
            <m-card plain icon="menu" title="对抗技巧">
              <p class="m-0">{{model.battleTips}}</p>
            </m-card>
            <m-card plain icon="menu" title="团战思路">
              <p class="m-0">{{model.teamTips}}</p>
            </m-card>
            <m-card plain icon="menu" title="英雄关系">
              <div class="fs-xl">最佳搭档</div>
              <div class="d-flex pt-3" v-for="item in model.partners" :key="item.name">
                <img :src="item.hero.avatar" height="50">
                <p class="flex-1 ml-3 m-0">{{item.description}}</p>
              </div>
              <div class="border-bottom mt-3"></div>
            </m-card>
          </div>
        </swiper-slide>
      </swiper>
    </div>
  </div>
</template>

<script>
import Card from '../components/Card'
export default {
  name: "hero",
  props: {
    id: {
      required: true
    }
  },
  components: {
    'm-card': Card
  },
  data() {
    return {
      model: null,
      currentSkillIndex: 0
    };
  },
  computed: {
    currentSkill() {
      return this.model.skills[this.currentSkillIndex];
    }
  },
  methods: {
    async fetch() {
      const { data } = await this.$http.get(`heroes/${this.id}`);
      this.model = data;
    }
  },
  created() {
    this.fetch();
  }
};
</script>

<style>
.topbar {
  position: sticky;
  top: 0;
  z-index: 999;
}
</style>
<style lang="scss">
@import "../assets/scss/_variable.scss";
.page-hero {
  .top {
    height: 50vw;
    background: #fff no-repeat top center;
    background-size: auto 100%;
  }
  .info {
    background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
    .scores {
      .badge {
        margin: 0 0.4rem;
        display: inline-block;
        width: 1rem;
        height: 1rem;
        line-height: 1rem;
        text-align: center;
        border-radius: 50%;
        font-size: 0.6rem;
        border: 1px solid rgba(255, 255, 255, 0.2);
      }
    }
  }
  .skills {
    img.icon {
      border: 3px solid map-get($colors, "white");
      border-radius: 50%;
      width: 70px;
      height: 70px;
      &.active {
        border-color: map-get($colors, "primary");
      }
    }
  }
  .hero-items {
    img.icon {
      width: 45px;
      height: 45px;
      border-radius: 50%;
    }
  }
}
</style>